<style scoped>
    .expand-row {
        margin-bottom: 16px;
    }
</style>
<template>
    <div>
        <Table :columns="columns10" :data="row.memberList"></Table>
    </div>
</template>
<script>

    export default {

        props: {
            row: Object,

        },

        data() {
            return {
                columns10: [
                    {
                        type: 'index',
                        title: '序号',
                        width: 80,
                        align: 'center'
                    },
                    {
                        title: '用户昵称',
                        key: 'username',
                        align: 'center'
                    },

                    {
                        title: 'K值',
                        key: 'key1',
                        align: 'center'
                    },
                    {
                        title: '天数',
                        key: 'dayTime',
                        align: 'center'
                    },
                    {
                        title: '创建时间',
                        key: 'time',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('div', {
                                }, this.timeFormat(params.row.time)),
                            ]);
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 320,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'info',
                                    },
                                    style: {
                                        display: 'inline-block',
                                        marginRight: '5px',
                                    },
                                    on: {
                                        click: () => {
                                            console.log(params.row);
                                            this.jumpReport(params.row.id,params.row.gameId)
                                        }
                                    }
                                }, '获取报表'),
                            ]);
                        }
                    }
                ],
            }
        },
        methods: {
            // 跳转到报表路由
            jumpReport(a,b){
                this.$router.push({ name: 'report',params: {userId:a,gameId:b}});
            },
            timeFormat(time) {
                var d = new Date(time);
                var year = d.getFullYear();       //年  
                var month = d.getMonth() + 1;     //月  
                var day = d.getDate();            //日  
                var hh = d.getHours();            //时  
                var mm = d.getMinutes();          //分  
                var ss = d.getSeconds();           //秒  
                var clock = year + "/";
                if (month < 10)
                    clock += "0";
                clock += month + "/";
                if (day < 10)
                    clock += "0";
                clock += day + " ";
                if (hh < 10)
                    clock += "0";
                clock += hh + ":";
                if (mm < 10) clock += '0';
                clock += mm + ":";
                if (ss < 10) clock += '0';
                clock += ss;
                return (clock);
            }

        },
        mounted() {


        }
    };
</script>
<style scoped>
    .addInput {
        display: inline-block;
        /* padding: 4px 7px; */
        width: 63px;
        height: 34px;
        line-height: 34px;
        color: #262b36;
        text-align: center;
        background-color: #E7EAED;
        border: 1px solid #dcdee2;
        border-right: none;
        border-radius: 4px 0 0 4px;
    }
</style>